import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-historique-restaurant',
  templateUrl: './historique-restaurant.page.html',
  styleUrls: ['./historique-restaurant.page.scss'],
})
export class HistoriqueRestaurantPage implements OnInit {

  showeEmptyHistoriqueMessage:boolean;
  showeHistoriqueList:boolean;
  commandes:any = [];
  restaurantCommande:any=[];
  commandeDate:Date;

  constructor() { }

  ngOnInit() {
    console.log(JSON.parse(localStorage.getItem('restaurationHistorique')))
    if(localStorage.getItem('restaurationHistorique')){
      this.showeHistoriqueList = true;
      this.showeEmptyHistoriqueMessage = false;
      this.commandes =  JSON.parse(localStorage.getItem('restaurationHistorique'));
      this.commandes.map(productList=>{
        console.log(productList)
      })
    }else{
      this.showeHistoriqueList = false;
      this.showeEmptyHistoriqueMessage = true;
    }
  }
}
